<template>
  <div class="resumeEditor" :class="{htmlMode:enableHtml}" ref="container">
    <div v-if="enableHtml" v-html="result"></div>
    <pre v-else>{{result}}</pre>
  </div>
</template>

<script>
import marked from "marked";
export default {
  props: ["markdown", "enableHtml"],
  name: "ResumeEditor",
  computed: {
    result: function() {
      var rendererMD = new marked.Renderer();
      //
      marked.setOptions({
        renderer: rendererMD,
        gfm: true, // Git Hubmarkdown.
        tables: true, // gfm true
        breaks: true, // gfm true
        pedantic: false, //false
        sanitize: false, //html
        smartLists: true, //markdown
        smartypants: false //
      });
      //https://github.com/markedjs/marked/issues/655#issuecomment-383226346
      const linkRenderer = rendererMD.link;
      rendererMD.link = (href, title, text) => {
        const html = linkRenderer.call(rendererMD, href, title, text);
        return html.replace(/^<a /, '<a target="_blank" rel="nofollow" ');
      };
      return this.enableHtml
        ? marked(this.markdown, { rendererMD })
        : this.markdown;
    }
  },
  methods: {
    goBottom: function() {
      this.$refs.container.scrollTop = 100000;
    },
    goTop: function() {
      this.$refs.container.scrollTop = 0;
    }
  }
};
</script>

<style scoped>
@media (max-width: 500px) {
  .resumeEditor {
  }
}
.htmlMode {
  animation: flip 2s;
}

@keyframes flip {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
</style>



