<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script>
import StyleEditor from "./components/StyleEditor";
import ResumeEditor from "./components/ResumeEditor";
import "./assets/reset.css";

export default {
  name: "app",
  components: {
    StyleEditor,
    ResumeEditor
  },
  data() {
    return {
      // 
      interval: 3,
      currentStyle: "",
      enableHtml: false,
      fullStyle: [
        `/*
* Inspired by http://strml.net/
*  https://gitee.com/zhousiwei/anires
* -
* 
*/

/*  */
* {
  transition: all .1s;
}
/*  */
html {
  color: rgb(222,222,222); background: rgb(0,43,54);
}

/*  */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  margin: .5em;
  overflow: auto;
  width: 45vw; height: 90vh;
  /* background: rgb(20,20,20); */
}
/*  */
.token.selector{ color: rgb(133,153,0); }
.token.property{ color: rgb(187,137,0); }
.token.punctuation{ color: yellow; }
.token.function{ color: rgb(42,161,152); }

/* 3D */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 0;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(10deg) translateZ(-100px) ;
          transform: rotateY(10deg) translateZ(-100px) ;
}

/*  */
.resumeEditor{
  position: fixed; right: 0; top: 0;
  padding: .5em;  margin: .5em;
  width: 48vw; height: 90vh;
  border: 1px solid;
  background: white; color: #222;
  overflow: auto;
}
/*  */
`,
        `
/*MarkdownHTML
 *HTML
*/
.resumeEditor{
  padding: 2em;
}
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #ddd;
}
`
      ],
      currentMarkdown: "",
      fullMarkdown: `

----
23JavaJava EE 

<br/>

- [](https://zhousiwei.gitee.io/cv/)


----
* 
* 
* 
* 
* /
* API
* Linux
* 
* 


----
  - **Java**: SpringBootSpringCloudSpringMVCMyBatisShiroFreemarker
  - ****: VueJsBootstrapLayUIjQuery UI
  - ****: MySQL/MariaDBSQLServerOracleMongoDBredismemcached
  - **web **: NginxTomcatApacheJetty
  - **OS**: LinuxWindows
  - **Others**: GitSvnMavenXMindVisioIDEA


----
1. [](http://www.hztywl.cn/)
2. [](http://www.3xdata.cn/)
3. [](http://jxyq123.51sole.com/)


----
1. [SpringBoot + Shiro + MyBatisPlus](https://gitee.com/zhousiwei/bootplus)
2. [SpringBootMyBatisPlusShiro](https://gitee.com/zhousiwei/springboot_mybatisplus)
3. [spring boot starter for fastjson](https://gitee.com/zhousiwei/fastjson-spring-boot-starter)
4. [webkettle,,ETLB/S](https://github.com/JoeyBling/webkettle)
5. [](https://gitee.com/zhousiwei/anires)
6. [VuePress](https://gitee.com/zhousiwei/ibooks)
7. [GitBook](https://zhousiwei.gitee.io/myBook/)
8. [-](https://gitee.com/zhousiwei/zhousiwei)


----
* [](https://zhousiwei.gitee.io/)
* [GitHub](https://github.com/JoeyBling)
* [](https://www.jianshu.com/u/02cbf31a043a)
* [](https://zhousiwei.gitee.io/ibooks)

[](https://zhousiwei.gitee.io/)
----
1. [Java](https://zhousiwei.gitee.io/tags/Java/)
2. [Linux](https://zhousiwei.gitee.io/tags/Linux/)
3. [ELK](https://zhousiwei.gitee.io/tags/ELK%E6%97%A5%E5%BF%97%E5%88%86%E6%9E%90/)
4. [MySQL](https://zhousiwei.gitee.io/tags/MySQL)
5. [Hexo](https://zhousiwei.gitee.io/tags/Hexo)
6. [](https://zhousiwei.gitee.io/tags/%E6%97%A5%E8%AE%B0%E6%9C%AC/)


----
* QQ**2434387555** | **13647910412**
* **Java****************Linux**

> Fork [](https://gitee.com/zhousiwei/anires)
`
    };
  },
  created() {
    this.makeResume();
  },

  methods: {
    makeResume: async function() {
      await this.progressivelyShowStyle(0);
      await this.progressivelyShowResume();
      await this.progressivelyShowStyle(1);
      await this.showHtml();
      await this.progressivelyShowStyle(2);
    },
    showHtml() {
      return new Promise((resolve, reject) => {
        this.enableHtml = true;
        resolve();
      });
    },
    progressivelyShowStyle(n) {
      return new Promise((resolve, reject) => {
        let interval = this.interval;
        let showStyle = async function() {
          let style = this.fullStyle[n];
          if (!style) {
            return;
          }
          //  n  style 
          let length = this.fullStyle
            .filter((_, index) => index <= n)
            .map(item => item.length)
            .reduce((p, c) => p + c, 0);
          let prefixLength = length - style.length;
          if (this.currentStyle.length < length) {
            let l = this.currentStyle.length - prefixLength;
            let char = style.substring(l, l + 1) || " ";
            this.currentStyle += char;
            if (style.substring(l - 1, l) === "\n" && this.$refs.styleEditor) {
              this.$nextTick(() => {
                this.$refs.styleEditor.goBottom();
              });
            }
            setTimeout(showStyle, interval);
          } else {
            resolve();
          }
        }.bind(this);
        showStyle();
      });
    },
    progressivelyShowResume() {
      return new Promise((resolve, reject) => {
        let length = this.fullMarkdown.length;
        let interval = this.interval;
        let showResume = () => {
          if (this.currentMarkdown.length < length) {
            this.currentMarkdown = this.fullMarkdown.substring(
              0,
              this.currentMarkdown.length + 1
            );
            let lastChar = this.currentMarkdown[
              this.currentMarkdown.length - 1
            ];
            let prevChar = this.currentMarkdown[
              this.currentMarkdown.length - 2
            ];
            if (prevChar === "\n" && this.$refs.resumeEditor) {
              this.$nextTick(() => this.$refs.resumeEditor.goBottom());
            }
            setTimeout(showResume, interval);
          } else {
            resolve();
          }
        };
        showResume();
      });
    }
  }
};
</script>

<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  min-height: 100vh;
}

* {
  box-sizing: border-box;
}
</style>



